@import url('https://fonts.googleapis.com/css2?family=Lato&family=Noto+Sans+KR&display=swap');


body {
    margin: 0;
    font-family: 'Noto Sans KR', sans-serif;
    overflow-x: hidden;
}

/*--------------------------------header--------------------------------*/

.header {
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo {
    padding: 17px 15px 17px 0px;
}

.brand {
    width: 55%;
    padding-left: 15px;
    border-left: 1px solid grey;
    position: relative;
}

.brand button {
    background-color: white;
    color: #777777;
    border: none;
    display: flex;
    align-items: center;
    font-size: 14px;
    padding: 0px;
    cursor: pointer;
}

.brand button svg {
    padding-left: 10px;
    font-size: 17px;
    fill: #555555;
}

.hidden_brand {
    display: flex;
    flex-direction: column;
    position: absolute;
    list-style: none;
    top: 32px;
    left: 15px;
    padding: 0;
    margin: 0;
    z-index: 9999;
}

.hidden_brand li {
    background-color: white;
}

.hidden_brand li:first-child {
    border-bottom: 1px solid grey;
}

.hidden_brand li:last-child {
    border-top: 1px solid grey;
}

.hidden_brand li img {
    padding: 15px 10px 15px 10px;
}

.language button {
    color: #555555;
    background-color: white;
    border: none;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: bold;
    padding: 0;
    cursor: pointer;
}

.language button svg {
    padding-right: 6px;
    font-size: 15px;
    fill: #555555;
}

/*--------------------------------main_banner--------------------------------*/

.main_banner img {
    width: 100%;
    max-height: 75vh;
    overflow: hidden;
    cursor: pointer;
}

.main_banner_menubar {
    display: flex;
    position: absolute;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 90px;
}

.main_banner_menubar img {
    filter: invert(100%);
    width: 118px;
    height: 36px;
    margin-right: 15px;
}

.main_banner_menu ol {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
}

.main_banner_menu ol li {
    color: white;
    padding: 9px 20px 9px 20px;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
}

.main_banner_icon ol {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
}

.main_banner_icon ol li {
    fill: white;
    padding: 9px 15px 9px 15px;
    cursor: pointer;
}

.main_banner_icon ol li:last-child {
    fill: white;
    padding: 9px 0px 9px 15px;
}

.main_banner_text {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 200px;
    left: 350px;
}

.main_banner_text h2 {
    color: white;
    font-size: 50px;
    margin: 40px 0px 0px 0px;
}

.main_banner_text p {
    color: white;
    font-size: 22px;
}

.main_banner_text button {
    width: 233px;
    font-size: 15px;
    padding: 14px 25px 14px 25px;
    font-weight: bold;
    border: 2px solid white;
}

.main_banner_text button:hover {
    border: 2px solid white;
    color: white;
    background-color: #4f7eab;
    transition: 0.3s;
}

.main_banner1 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/*--------------------------------shop_banner--------------------------------*/

.shop_banner {
    display: flex;
    justify-content: center;
    margin-top: 80px;
    overflow: hidden;
}

.shop_banner_container {
    width: 20%;
    overflow: hidden;
    padding: 15px;
}

.shop_banner_container div {
    display: flex;
    flex-direction: column;
}

.shop_banner_container h3:hover {
    text-decoration: underline;
    cursor: pointer;
}

.shop_banner_container img {
    cursor: pointer;
}

.shop_banner_container a {
    display: flex;
    align-items: center;
    color: black;
    font-size: 14px;
    font-weight: 600;
    text-decoration: none;
}

.shop_banner_container a:hover {
    text-decoration: underline;
}

.shop_banner_container a svg {
    padding-top: 2px;
    padding-left: 8px;
    font-size: 15px;

}

.shop_banner_container div a {
    margin: 6px 0px 6px 0px;
}

