.scroll_slide_box {
    display: flex;
    width: 100vw;
    height: 700px;
    position: relative;
    margin-top: 90px;
}

.scroll_slide_box_vertical {
    position: absolute;
    display: flex;
}

.scroll_slide_box_container0 {
    display: grid;
    grid-template-areas:
        "A A D "
        "A A D "
        "C B D ";
    column-gap: 20px;
    row-gap: 20px;
    scroll-snap-align: center;
    padding: 0px 20px 0px 40px;
    margin: 0px 0px 0px 311px;
}

